.main{
    display: grid;
    box-sizing: border-box;
    width: 100%;        
    height: 100vh;    
    grid-template-rows: 1fr 1fr 1fr 1fr ;

    &__section{
        display: grid;
        width: 100%;
    }
    &-header{
        display: grid;
        padding-top: 30px;
        padding-bottom: 200px;
        width: 100%;
        height: 640px;
        background: url(../asset/main__bg.png) center center/cover no-repeat;

        &__navbar{           
            padding-bottom: 110px;
            padding-left: 150px;
            justify-self: start;            
        }
        &__title{
            font-weight: 700;
            font-size: 40px;
            line-height: 58px;
            color: #FFFFFF;
            padding-bottom: 20px;
            text-align: center;
        }
        &__separator{
            margin-bottom: 35px;   
        }
        &__description{
            font-weight: 700;
            font-size: 24px;
            line-height: 35px;
            color: #FFFFFF;
            text-align: center;
        }
        &__button{
            justify-self: center;
        }
    }
    &-about{
        width: 100%;
        padding: 80px 0 101px 0;
        &__title{
            display: grid;
            justify-self: center;
            padding-bottom: 20px;
        }
        &__separator{       
            margin-bottom: 35px;  
        }
        &__text{
            display: grid;
            justify-self: center;
            max-width: 590px;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            text-align: center;

            color: #000000;
        }
    }
    &-best{
        padding: 80px 0 110px 0;
        background: url(../asset/best__bg.png) center center/cover no-repeat;
        &__title{
            display: grid;
            justify-self: center;
            margin-bottom: 39px;
        }
        &__products{
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            grid-gap: 70px;
            justify-self: center;

            &-card{

            }
        }
    }
    &-footer{
        min-height: 150px; 
        &__separator{       
            margin-bottom: 35px;  
        }
        &__navbar{
            justify-self: center;
            padding: 50px 0 30px  0;
        }
    }
}

.navbar{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
    position: relative;
    &:first-child{
        &:after{
            position: absolute;
            background:url(../asset/coffee-beanses.svg) no-repeat;
        }        
    }
    &-link{
        display: grid;
        justify-content: center;
        align-items: center;
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        color: #FFFFFF;
        position: relative;
        transition: 0.3s all ease;
        text-align: center;
        box-sizing:border-box;
        border: 1px solid transparent;
        cursor: pointer;            
        &:hover{ 
            border-radius:3px;           
            border: 1px solid #FFFFFF;  
            background-color: rgba(255, 255, 255, 0.25)
        }
        &-black{
            color: #000000;
            &:hover{ 
                border-radius:3px;           
                border: 1px solid #000000;  
                background-color: rgba(0, 0, 0, 0.15)
            }
        } 
    }
}
.separator{
    display: grid;
    align-items: center;
    grid-template-columns: auto auto auto;
    grid-gap: 25px;     
    justify-self: center;
    &__right-line{
        width: 60px;
        height: 2px;
        border: 1px solid #FFFFFF;
        &_black{
            border: 1px solid #000000;
        }
    }
    &__left-line{
        width: 60px;
        height: 2px;
        border: 1px solid #FFFFFF;
        &_black{
            border: 1px solid #000000;
        }
    }
    &__icon{            
        width: 29.3px;    
        height: 29.3px;
        background:url(../asset/coffee-beans.svg) no-repeat;
        &_black{
            background:url(../asset/coffee-beans_black.svg) no-repeat;
        }
    }
}
.title{
    font-weight: 400;
    font-size: 24px;
    line-height: 35px;
    color: #000000;
}
.button{ 
    display: grid;
    justify-content: center;
    align-items: center;  
    font-weight: 700;
    border: 1px solid #FFFFFF;
    border-radius: 3px;    
    font-size: 14px;
    line-height: 20px;    
    color: #FFFFFF;   
    transition: 0.3s all ease;
    cursor: pointer;     
    &-more{
        width: 120px;
        height: 30px;
        &:hover{ 
            background-color: rgba(255, 255, 255, 0.25) 
        }
    }
    
}
.card{
    display: grid;
    width: 220px;
    height: 240px;
    background: rgba(255, 255, 255, 0.65);
    border-radius: 8px;
    padding: 22px 22px 22px 21px;
    transition: 0.5s all ease; 
    cursor: pointer;   
    &:hover{        
        box-shadow: -6px -7px 21px 5px rgba(166, 130, 130, 0.75);
    }
    &__img{
        justify-self: center;
        margin-bottom: 14px;
    }
    &__descr{
        display: grid;
        align-self: end;

        &-title{
                justify-self: end;
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
                text-align: center;
                color: #000000;
                margin-bottom: 10px;
            }
            &-price{
                justify-self: end;
                font-weight: 700;
                font-size: 14px;
                line-height: 20px;
                text-align: center;
                color: #000000;
            }
    }
    
}